<template>
    <div class="dashboard">
        <Sidebar></Sidebar>
        <div class="content" :class="drawer == false ? 'margin-zero' : '' ">
            <Navbar></Navbar>
            <div class="views">
                <router-view/>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Sidebar from '@/components/Sidebar.vue'
import Navbar from '@/components/Navbar.vue'
export default {
    components: {
        Sidebar,
        Navbar
    },
    data: () => ({

    }),
    async mounted() {
        await this.refresh()
    },
    computed: {
        ...mapState({
            drawer: state => state.categories.drawer,
            user: state => state.main.user,
            one_user: state => state.main.one_user,
            profile: state => state.main.profile
        }),
    },
    methods: {
        ...mapActions({
            fetchOneUser: 'main/fetchOneUser'
        }),
        // ============= REFRESH START =============
        async refresh(){
            this.loading = true
            await this.fetchOneUser(this.user.user_id)

            this.profile.photo = this.one_user.image
            this.profile.login = this.one_user.email
            this.profile.fullname = this.one_user.name

            this.loading = false
        },
        // ============= REFRESH START =============
    }

};
</script>

<style >
.margin-zero{
    width: 100% !important;
    margin-left: 0%!important;
}

</style>