<template>
    <div>
        <div class="bar">
            <div class="d-flex align-center searchInputWrapper">
                <v-btn icon class="mr-2 burger_button" @click="changeDrawer">
                    <img src="@/assets/icons/menu.svg" alt="menu">
                </v-btn>
                <div class="searchInputWrapper" v-if="$can('read', 'navbar_search')">
                    <img src="@/assets/icons/search-normal.svg" alt="search-normal"
                         class="searchInputIcon navbarDetails">
                    <input type="text" v-model="searchValue" debounce="500"
                           class="searchInput navbarDetails" placeholder="Qidiruv">
                </div>
            </div>

            <div class="mr-2  align-center d-flex">
                <modalCategoryAndLists v-if="$can('read', 'navbar_categories_list')"></modalCategoryAndLists>
                <v-btn icon class="mr-4" to="/codebase" v-if="$can('read', 'navbar_codebase')">
                    <img src="@/assets/icons/scan-barcode.svg" alt="">
                </v-btn>


                <screenshot v-if="$can('read', 'navbar_screenshot')"></screenshot>
                

                <v-menu
                    v-model="menu"
                    :close-on-content-click="false"
                    :nudge-width="220"
                    offset-x
                    offset-y
                >
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn icon elevation="0">
                            <v-avatar>
                                <img
                                    :src="one_user.image ? baseURL + '/public/images/' +  one_user.image : require('@/assets/profile-picture.jpg')"
                                    alt="John"
                                    v-bind="attrs"
                                    v-on="on"
                                    style="object-fit: cover;"
                                >
                            </v-avatar>
                        </v-btn>
                    </template>

                    <v-card class="px-6">

                        <router-link to="/profile" tag="a" class="border-bottom pb-4 pt-6 d-flex justify-space-between"
                        v-if="$can('read', 'navbar_profile')">
                            <span class="blue--text">Profil</span>
                            <img src="@/assets/images/arrow-right.png" alt="arrow-right">
                        </router-link>

                        <router-link to="/roles" tag="a" class="border-bottom py-4 d-flex justify-space-between"
                        v-if="$can('read', 'navbar_menu_roles')">
                            <span class="blue--text">Ruxsatlar</span> 
                            <img src="@/assets/images/arrow-right.png" alt="arrow-right">
                        </router-link>

                        <router-link to="/users" tag="a" class="border-bottom py-4 d-flex justify-space-between"
                        v-if="$can('read', 'navbar_users_list')">
                            <span class="blue--text">Foydalanuvchilar</span> 
                            <img src="@/assets/images/arrow-right.png" alt="arrow-right">
                        </router-link>

                        <!-- <div class="border-bottom d-flex justify-space-between py-4">
                            <span class="blue--text">Abror Xoshimjonov</span>
                            <img src="@/assets/images/arrow-right.png" alt="arrow-right">
                        </div> -->

                        <v-btn
                            class="primaryBtn my-4"
                            text style="width: 100%"
                            prepend-inner-icon="mdi-exit-to-app"
                            @click="logOutFromSystem()"
                        >
                            <img src="@/assets/images/logout.png" alt="logout" width="20" class="mr-2">
                                Chiqish
                        </v-btn>
                    </v-card>
                </v-menu>
            </div>
        </div>
        <div class="toolbar" v-html="changeNav"></div>
    </div>
</template>

<script>
import {mapGetters, mapState, mapMutations, mapActions} from 'vuex'
import modalCategoryAndLists from '@/components/modals/Lists/Index'
import screenshot from '../components/Screenshot'
import { BASE_URL } from '@/plugins/variables'
import { routesMixin } from '@/mixins/routeNames'


export default {
    mixins: [routesMixin],
    components: {
        modalCategoryAndLists,
        screenshot,
    },
    data() {
        return {
            menu: false,
            baseURL: '',
        }
    },
    async mounted(){
        this.baseURL = BASE_URL
    },
    computed: {
        ...mapState(
        {
            drawer: state => state.categories.drawer,
            one_user: state => state.main.one_user,
        }),
        checkWindowWidth(){
            if(window.screen.width < '1000'){
                return true
            }else{
                return false
            }
        },
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
        searchValue: {
            get: function() {
                return this.$store.state.main.searchValue
            },
            set: function(newValue) {
                return this.$store.state.main.searchValue = newValue
            }
        },
        changeNav() {
            switch (this.$route.name) {
                case 'home':
                    this.routeNAME='home'
                    return 'Umumiy'
                case 'roles':
                    this.routeNAME='roles'
                    return ''
                case 'codebase':
                    this.routeNAME='codebase-ip'
                    return 'Kodlar bazasi'
                case 'users':
                    this.routeNAME='users'
                    return 'Foydalanuvchilar'
                case 'finance':
                    this.routeNAME='finance-kirim'
                    return 'Moliya'
                case 'warehouse-ip':
                    this.routeNAME='warehouse-ip-kirim'
                    return '<span class="text-muted">Omborxona</span><span class="mr-2 ml-2 text-muted">/</span>Ip'
                case 'warehouse-mato':
                    this.routeNAME='warehouse-mato-kirim'
                    return '<span class="text-muted">Omborxona</span><span class="mr-2 ml-2 text-muted">/</span>Mato'
                case 'warehouse-yoqa':
                    this.routeNAME='warehouse-yoqa-kirim'
                    return '<span class="text-muted">Omborxona</span><span class="mr-2 ml-2 text-muted">/</span>Yoqa'
                case 'outsource-mato-toquv-id':
                    this.routeNAME='outsource-mato-kirim'
                    return `<span class="text-muted">Outsource</span><span class="mr-2 ml-2 text-muted">/</span><span class="text-muted">Mato to\`quv</span><span class="mr-2 ml-2 text-muted">/</span>` + this.$store.state.main.activeRouteName
                case 'outsource-yoqa-toquv-id':
                    this.routeNAME='outsource-yoqa-kirim'
                    return `<span class="text-muted">Outsource</span><span class="mr-2 ml-2 text-muted">/</span><span class="text-muted">Yoqa to\`quv</span><span class="mr-2 ml-2 text-muted">/</span>` + this.$store.state.main.activeRouteName
                case 'outsource-boyoqxona-id':
                    this.routeNAME='outsource-painting-kirim'
                    return '<span class="text-muted">Outsource</span><span class="mr-2 ml-2 text-muted">/</span><span class="text-muted">Bo`yoqxona</span><span class="mr-2 ml-2 text-muted">/</span>' + this.$store.state.main.activeRouteName
                case 'supply-tayyor-xomashyo-mato':
                    this.routeNAME='supply-ready-mato-kirim'
                    return `<span class="text-muted">Ta\`minot</span><span class="text-muted mr-1 ml-2">/</span><span class="text-muted">Tayyor xomashyo</span><span class="text-muted ml-2 mr-2">/</span>Mato`
                case 'supply-tayyor-xomashyo-yoqa':
                    this.routeNAME='supply-ready-yoqa-kirim'
                    return `<span class="text-muted">Ta\`minot</span><span class="text-muted mr-1 ml-2">/</span><span class="text-muted">Tayyor xomashyo</span><span class="text-muted ml-2 mr-2">/</span>Yoqa`
                case 'supply-aksessuar':
                    this.routeNAME='supply-aksessuar-kirim'
                    return '<span class="text-muted">Ta\`minot</span><span class="mr-2 ml-2 text-muted">/</span>Aksessuarlar'
                case 'supply-bichuv':
                    this.routeNAME='supply-bichuv-buyurtma-tarixi'
                    return '<span class="text-muted">Ta\`minot</span><span class="mr-2 ml-2 text-muted">/</span>Bichuv'
                case 'factory-id':
                    this.routeNAME='factory-hozirgi-process'
                    return `<span class="text-muted">Fabrika</span><span class="mr-2 ml-2 text-muted">/</span>` + this.$store.state.main.activeRouteName
                case 'ready-products-id':
                    this.routeNAME='ready-products-kirim'
                    return `<span class="text-muted">Tayyor mahsulotlar</span><span class="mr-2 ml-2 text-muted">/</span>` + this.$store.state.main.activeRouteName
                default:
                    break;
            }
        }
    },
    methods: {
        ...mapMutations(
            {
                SET_DRAWER: 'categories/SET_DRAWER',
            }
        ),
        ...mapActions(
            {
                LOG_OUT: 'main/LOG_OUT'
            }
        ),
        changeDrawer() {
            this.SET_DRAWER(!this.drawer)
        },
        async logOutFromSystem(){
            await this.LOG_OUT()
            this.$router.push({name: 'login'})
        }
    },
    watch: {
        searchValue(newVal) {
            if(newVal || newVal == ''){
                // cancel pending call
                clearTimeout(this._timerId)

                // delay new call 500ms
                this._timerId = setTimeout(() => {
                    // this.$store.dispatch('main/getFilteredData',{ searchValue: newVal, routeURL: this.routeURL})

                    this.routeNames()
                    
                }, 400)
            }
        },
    }

}
</script>

<style lang="scss">
.bar {
    width: 100% !important;
    height: 80px !important;
    display: flex;
    align-items: center;
    justify-content: space-between !important;
    padding: 20px 20px 20px 10px;
    background: #FDFDFD !important;
}

.toolbar {
    font-style: normal;
    font-weight: 500;
    font-size: 33px;
    padding: 16px;
    background: #FDFDFD;
    border-top: 2px solid #F5F5F5;
    border-bottom: 2px solid #F5F5F5;
}

.v-menu__content {
    border-radius: 14px !important;
}

.navbarDetails {
    display: flex;
}

@media (max-width: 600px) {
    .navbarDetails {
        display: none;
    }
}
</style>

